export default {
  calculators: state => {
    return state.calculators;
  },
  activeCalculatorIndex: state => {
    return state.activeCalculator;
  },
  activeCalculator: state => {
    return state.calculators[state.activeCalculator];
  },
  attacker: state => {
    return state.calculators[state.activeCalculator].attacker;
  },
  defender: state => {
    return state.calculators[state.activeCalculator].defender;
  },
  options: state => {
    return state.calculators[state.activeCalculator].options;
  },
  calculationStarted: state => {
    return state.calculationStarted;
  }
};
