export default {
  addCalculator({ state, commit }) {
    if (state.calculators.length >= 5) {
      return;
    }
    const calculator = {
      attacker: {
        creature: {},
        hero: {
          level: 0,

          stats: {
            attack: 0,
            defense: 0
          },
          skills: {
            offense: 0,
            armorer: 0,
            archery: 0,
            artillery: 0,
            air: 0,
            fire: 0,
            earth: 0,
            water: 0
          }
        },

        minDamage: 0,
        maxDamage: 0,
        averageDamage: 0,

        minKills: 0,
        maxKills: 0,
        averageKills: 0
      },
      defender: {
        creature: {},
        hero: {
          level: 0,

          stats: {
            attack: 0,
            defense: 0
          },
          skills: {
            offense: 0,
            armorer: 0,
            archery: 0,
            artillery: 0,
            air: 0,
            fire: 0,
            earth: 0,
            water: 0
          }
        },

        minDamage: 0,
        maxDamage: 0,
        averageDamage: 0,

        minKills: 0,
        maxKills: 0,
        averageKills: 0
      },
      options: {
        terrain: ""
      }
    };
    commit("ADD_CALCULATOR", calculator);
    commit("SET_ACTIVE_CALCULATOR", state.calculators.length - 1);
  },
  setActiveCalculator({ commit }, calculatorIndex) {
    commit("SET_ACTIVE_CALCULATOR", calculatorIndex);
  },
  setCreaturesCount({ commit }, { side, count }) {
    commit("SET_CREATURES_COUNT", { side, count });
    commit("START_CALCULATION");
  },
  setActiveCreature({ commit }, { side, creature }) {
    creature = {
      ...creature,
      count: 0,
      effects: []
    };
    commit("SET_ACTIVE_CREATURE", { side, creature });
    commit("START_CALCULATION")
  },
  deleteCalculator({ commit, state }, index) {
    if (state.calculators.length > 1) {
      commit("DELETE_CALCULATOR", index);

      if (state.activeCalculator === index) {
        if (state.calculators.length > 1) {
          commit("SET_ACTIVE_CALCULATOR", index - 1);
        } else {
          commit("SET_ACTIVE_CALCULATOR", 0);
        }
      }
    }
  },
  setHeroStat({ commit }, { side, stat, value }) {
    commit("SET_HERO_STAT", { side, stat, value });
    commit("START_CALCULATION");
  },
  setAttackerHero({ commit }, hero) {
    hero = {
      ...hero,
      level: 0
    };
    commit("SET_ATTACKER_HERO", hero);
    commit("START_CALCULATION");
  },
  setTotalDamages({ commit }, { side, damages }) {
    damages = {
      minDamage: damages.minDamage,
      maxDamage: damages.maxDamage,
      averageDamage: damages.averageDamage,
      minKills: damages.minKills,
      maxKills: damages.averageKills,
      averageKills: damages.averageKills
    };
    commit("SET_TOTAL_DAMAGES", { side, damages });
    commit("START_CALCULATION");
  },
  startCalculation({ commit }) {
    commit("START_CALCULATION");
  },
  stopCalculation({ commit }) {
    commit("STOP_CALCULATION");
  }
};
