export default {
  async getHeroes({ commit }) {
    const heroes = require(/* webpackChunkName: 'data' */ "@/assets/json/heroes.json");
    commit("SET_HEROES", heroes);
  },
  async getCreatures({ commit }) {
    const creatures = require(/* webpackChunkName: 'data' */ "@/assets/json/creatures.json");
    commit("SET_CREATURES", creatures);
  },
  async getTerrains({ commit }) {
    const terrains = require(/* webpackChunkName: 'data' */ "@/assets/json/terrains.json");
    commit("SET_TERRAINS", terrains);
  },
  async getCreaturesHates({ commit }) {
    const hates = require(/* webpackChunkName: 'data' */ "@/assets/json/creatures_hates.json");
    commit("SET_CREATURES_HATES", hates);
  }
};
