export default {
  ADD_CALCULATOR(state, calculator) {
    state.calculators.push(calculator);
  },
  SET_ACTIVE_CALCULATOR(state, calculatorIndex) {
    state.activeCalculator = calculatorIndex;
  },
  SET_TERRAIN(state, terrain) {
    state.calculators[state.activeCalculator].options.terrain = terrain;
  },

  ADD_CREATURE(state, { side, creature }) {
    state.calculators[state.activeCalculator][side].creatures.push(creature);
  },

  SET_HERO(state, { side, hero }) {
    state.calculators[state.activeCalculator][side].hero = hero;
  },

  SET_HERO_STAT(state, { side, stat, value }) {
    state.calculators[state.activeCalculator][side].hero.stats[stat] = value;
  },

  SET_CREATURES_COUNT(state, { side, count }) {
    state.calculators[state.activeCalculator][side].creature.count = count;
  },

  SET_ACTIVE_CREATURE(state, { side, creature }) {
    state.calculators[state.activeCalculator][side].creature = {
      ...state.calculators[state.activeCalculator][side].creature,
      ...creature
    };
  },
  DELETE_CALCULATOR(state, index) {
    state.calculators.splice(index, 1);
  },

  SET_DAMAGES(state, { side, minDamage, averageDamage, maxDamage }) {
    state.calculators[state.activeCalculator][side] = {
      ...state.calculators[state.activeCalculator][side],
      minDamage,
      averageDamage,
      maxDamage
    };
  },

  SET_ATTACKER_HERO(state, hero) {
    state.calculators[state.activeCalculator].attacker.hero = {
      ...state.calculators[state.activeCalculator].attacker.hero,
      hero
    };
  },
  SET_TOTAL_DAMAGES(state, { side, damages }) {
    state.calculators[state.activeCalculator][side] = {
      ...state.calculators[state.activeCalculator][side],
      ...damages
    };
  },
  START_CALCULATION(state) {
    state.calculationStarted = true;
  },
  STOP_CALCULATION(state) {
    state.calculationStarted = false;
  }
};
