export default {
  SET_HEROES(state, heroes) {
    state.data.heroes = heroes;
  },
  SET_CREATURES(state, creatures) {
    state.data.creatures = creatures;
  },
  SET_TERRAINS(state, terrains) {
    state.data.terrains = terrains;
  },
  SET_HATES(state, hates) {
    state.data.hates = hates;
  }
};
