import i18n from "@/i18n";

export default [
  {
    path: "/",
    component: () => import(/* webpackChunkName: 'home' */ "@/views/Home"),
    meta: {
      title: "Home"
    }
  },
  {
    path: "/DamageCalculator",
    component: () =>
      import(
        /* webpackChunkName: 'DamageCalculator' */ "@/views/DamageCalculator"
      ),
    props: true,
    meta: {
      title: i18n.t("Damage calculator")
    }
  },

  {
    path: "*",
    component: () =>
      import(/* webpackChunkName: 'error' */ "@/views/Error.vue"),
    meta: {
      title: "Error"
    }
  }
];
